import {Component, OnInit} from '@angular/core';
import {Http} from '@angular/http';
import {ReportJson} from './entity/report-json';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnInit {

    cn: any;
    userList = [{
        label: '操作员',
        value: null,
    }];
    reportJson: ReportJson;
    // TODO:发布时要设成''
    host = '';

    constructor(private http: Http) {
    }

    ngOnInit() {
        this.reportJson = new ReportJson;
        this.reportJson.endTime = new Date();
        this.reportJson.endTime.setHours(9);
        this.reportJson.endTime.setMinutes(0);
        this.reportJson.endTime.setSeconds(0);
        this.reportJson.endTime.setMilliseconds(0);
        this.reportJson.beginTime = new Date(this.reportJson.endTime);
        this.reportJson.beginTime.setDate(this.reportJson.endTime.getDate() - 1);
        this.cn = {
            firstDayOfWeek: 0,
            dayNames: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
            dayNamesShort: ['日', '一', '二', '三', '四', '五', '六'],
            dayNamesMin: ['日', '一', '二', '三', '四', '五', '六'],
            monthNames: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
            monthNamesShort: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月']
        };
        this.http.post(this.host + 'userGet', {})
            .subscribe((data) => {
                let userList = data.json();
                for (let user of userList) {
                    this.userList.push({
                        label: user.userId,
                        value: user.userId,
                    });
                }
            });
    }

    query() {
        this.http.post(this.host + 'getShenBeiTiCheng', this.reportJson)
            .subscribe((i) => {
                window.open(this.host + 'receipt/' + i.json());
            });
    }
}



